import * as React from "react";
import { Link, graphql } from "gatsby";
import Layout from "../components/Layout";

const IndexPage = ({ data }) => {
  const posts = data.allMdx.nodes;

  return (
    <Layout>
      {posts.map((mdx) => {
        return (
          <div key={mdx.id} className="flex flex-col gap-y-1">
            <div className="flex gap-x-2 text-sm">
              {mdx.frontmatter.tags &&
                mdx.frontmatter.tags.map((tag) => {
                  return (
                    <div
                      key={tag}
                      className="rounded-lg px-2 py-1 bg-violet-200"
                    >
                      #{tag}
                    </div>
                  );
                })}
            </div>
            <Link
              className="group flex flex-col gap-y-0.5"
              to={`/posts/${mdx.frontmatter.slug}`}
            >
              <h2 className="text-xl group-hover:underline group-hover:underline-offset-4">
                {mdx.frontmatter.title}
              </h2>
              {mdx.frontmatter.description && (
                <div className="text-slate-500">
                  {mdx.frontmatter.description}
                </div>
              )}
              <div>{mdx.frontmatter.date}</div>
            </Link>
          </div>
        );
      })}
      <div className="snb"></div>
    </Layout>
  );
};

export const query = graphql`
  query MyQuery {
    allMdx(
      sort: { frontmatter: { date: DESC } }
      filter: { internal: { contentFilePath: { regex: "/src/posts/" } } }
    ) {
      nodes {
        frontmatter {
          date(formatString: "MMMM D, YYYY")
          title
          description
          slug
          tags
        }
        id
      }
    }
  }
`;

export default IndexPage;

export const Head = () => <title>Home Page</title>;
